  export default {
    data() {
      return {
        result: '',
				scanType: '',
				
				codeUrl: '',
        isShow:true
      }
    },
	onLoad(options) {
		//动态设置标题
		if (options.label) {
		  uni.setNavigationBarTitle({
			title: options.label
		  })
		}
		uni.setNavigationBarColor({
		  frontColor: '#ffffff',
		  backgroundColor: '#63b4fe'
		})
		
		const self = this;
		//uni.scanCodeAPI暂时不支持h5，所以用别的方式

			uni.scanCode({
				success: function (res) {
					self.result = res.result;
					self.scanType = res.scanType;
				},
				fail: function(res){
					console.log('接口调用失败的回调函数（识别失败、用户取消等情况下触发）')
					console.log(res)
				},
				complete: function(error){
					console.log('接口调用结束的回调函数（调用成功、失败都会执行）')
					console.log(error)
				}
			});








	},
    methods: {
     //创建扫描控件
      startRecognize() {
        let that = this;
        if (!window.plus) return;
        that.isShow=false;
        // 创建条码扫描识别控件
        scan = new plus.barcode.Barcode('bcid');
        // 条码识别成功
        scan.onmarked = onmarked;
      },
	  onmarked(type, result, file) {
		  alert(type);
		  alert(result);
		  alert(file);
	    switch (type) {
	      case plus.barcode.QR:
	        type = 'QR';
	        break;
	      case plus.barcode.EAN13:
	        type = 'EAN13';
	        break;
	      case plus.barcode.EAN8:
	        type = 'EAN8';
	        break;
	      default:
	        type = '其它' + type;
	        break;
	    }
	    result = result.replace(/\n/g, '');
	    that.codeUrl = result;//扫描后返回值
	    alert(result);
	    scan.cancel();//关闭扫描
	    scan.close();//关闭条码识别控件
	    if(that.codeUrl){
	      that.isShow=true
	    }
	  },
      //开始扫描
      startScan() {
        if (!window.plus) return;
        this.startRecognize()//创建控件
        scan.start();
      },

      cancelScan(){
        this.isShow=true;
        scan.cancel();//关闭扫描
        scan.close();//关闭条码识别控件
      }
    }
  }

